let min = arr => {
  if(arr.length <= 1){return arr}
  if(arr.length > 2){
    return min([arr[0],min(arr.slice(1))])
  }else{
    let result = Math.min.apply(null,arr)
    return result
  }
}


//sort æŽ’åº å°†æ•°ç»„ä»Žå°åˆ°å¤§æŽ’åˆ— --é€’å½’æ–¹å¼
let sort = arr =>{
    if(arr.length<=1){return arr}
    let k =arr.length
    if( k > 2){
      let index = minIndex(arr)
      let min = arr[index]
      arr.splice(index,1)
      return [min].concat(sort(arr))
  }else{
    return arr[0]>arr[1]?arr.reverse():arr
  }
}



//sort æŽ’åº å°†æ•°ç»„ç”±å°åˆ°å¤§æŽ’åˆ— -- å¾ªçŽ¯æ–¹å¼

let swap =(arr,index,i)=>{
  let temp = arr[index]
  arr[index]= arr[i]
  arr[i]=temp
}

